@import url('https://fonts.cdnfonts.com/css/pt-sans-2');

body {
  margin: 0;
  font-family: 'PT Sans', sans-serif;
  /* min-height: 100%; */
  /* position: relative; */
  padding-bottom: 3rem;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient( rgba(49, 57, 73, 0.8) 20%, rgba(49, 57, 73, 0.5) 20%, rgba(49, 57, 73, 0.5) 35%, rgba(41, 48, 61, 0.6) 35%, rgba(41, 48, 61, 0.8) 45%, rgba(31, 36, 46, 0.5) 45%, rgba(31, 36, 46, 0.8) 75%, rgba(49, 57, 73, 0.5) 75%), linear-gradient(45deg, rgba(20, 24, 31, 0.8) 0%, rgba(41, 48, 61, 0.8) 50%, rgba(82, 95, 122, 0.8) 50%, rgba(133, 146, 173, 0.8) 100%) #313949;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}

code {
  font-family: 'PT Sans', sans-serif;
}
